.searchInput {
  display: flex;
  width: 100%;
  max-width: 1024px;
  justify-content: center;
  align-items: center;
  height: 80px;
  margin-top: 20px;
  border-radius: 10px;
}

.searchInput input {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  background-color: teal;
  height: 60px;
  outline: none;
  font-size: 20px;
  text-align: center;
  color: white;
}

.searchInput input::placeholder {
  color: rgba(255, 255, 255, 0.829);
}

.searchInput input:focus,
.searchInput input:hover {
  background-color: rgb(6, 134, 92);
}

@media (max-width: 1024px) {
  .searchInput {
    max-width: 768px;
  }
  .searchInput input {
    width: 100%;

    font-size: 20px;
  }
}

@media (max-width: 768px) {
  .searchInput {
    max-width: 480px;
  }
  .searchInput input {
    width: 100%;

    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .searchInput {
    max-width: 320px;
  }
  .searchInput input {
    width: 100%;
    font-size: 12px;
  }
}
